'use client';

import { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { AlertDialog, AlertDialogAction, AlertDialogCancel, AlertDialogContent, AlertDialogDescription, AlertDialogFooter, AlertDialogHeader, AlertDialogTitle, AlertDialogTrigger } from '@/components/ui/alert-dialog';
import { useCRMStore } from '@/hooks/useCRMStore';
import { Activity, ActivityType, ActivityTypeLabels, formatDate } from '@/lib/crmTypes';
import { ActivityForm } from './ActivityForm';

export function ActivityLog() {
  const { state, deleteActivity, getActivitiesByType, getActivitiesByDateRange } = useCRMStore();
  const [filterType, setFilterType] = useState<string>('');
  const [startDate, setStartDate] = useState('');
  const [endDate, setEndDate] = useState('');
  const [searchQuery, setSearchQuery] = useState('');

  // Get filtered activities
  const getFilteredActivities = (): Activity[] => {
    let activities = [...state.activities];

    // Filter by type
    if (filterType) {
      activities = activities.filter(activity => activity.type === filterType);
    }

    // Filter by date range
    if (startDate && endDate) {
      const start = new Date(startDate);
      const end = new Date(endDate);
      end.setHours(23, 59, 59, 999); // Include the entire end date
      activities = activities.filter(activity => {
        const activityDate = new Date(activity.date);
        return activityDate >= start && activityDate <= end;
      });
    }

    // Filter by search query
    if (searchQuery) {
      const query = searchQuery.toLowerCase();
      activities = activities.filter(activity =>
        activity.title.toLowerCase().includes(query) ||
        activity.description.toLowerCase().includes(query)
      );
    }

    // Sort by date (most recent first)
    return activities.sort((a, b) => new Date(b.date).getTime() - new Date(a.date).getTime());
  };

  const getContactName = (contactId?: string): string => {
    if (!contactId) return '';
    const contact = state.contacts.find(c => c.id === contactId);
    return contact ? contact.name : 'Contacto no encontrado';
  };

  const getOpportunityTitle = (opportunityId?: string): string => {
    if (!opportunityId) return '';
    const opportunity = state.opportunities.find(o => o.id === opportunityId);
    return opportunity ? opportunity.title : 'Oportunidad no encontrada';
  };

  const handleDeleteActivity = (activityId: string) => {
    deleteActivity(activityId);
  };

  const clearFilters = () => {
    setFilterType('');
    setStartDate('');
    setEndDate('');
    setSearchQuery('');
  };

  const filteredActivities = getFilteredActivities();

  const getActivityTypeColor = (type: ActivityType) => {
    switch (type) {
      case ActivityType.LLAMADA:
        return 'bg-blue-100 text-blue-800';
      case ActivityType.CORREO:
        return 'bg-green-100 text-green-800';
      case ActivityType.REUNION:
        return 'bg-purple-100 text-purple-800';
      case ActivityType.TAREA:
        return 'bg-orange-100 text-orange-800';
      default:
        return 'bg-gray-100 text-gray-800';
    }
  };

  const ActivityCard = ({ activity }: { activity: Activity }) => {
    const contactName = getContactName(activity.contactId);
    const opportunityTitle = getOpportunityTitle(activity.opportunityId);
    const isToday = new Date(activity.date).toDateString() === new Date().toDateString();
    const isPast = new Date(activity.date) < new Date();

    return (
      <Card className={`hover:shadow-md transition-shadow ${isToday ? 'border-blue-200 bg-blue-50' : ''}`}>
        <CardContent className="p-4">
          <div className="flex justify-between items-start mb-3">
            <div className="flex-1">
              <div className="flex items-center gap-2 mb-2">
                <Badge className={getActivityTypeColor(activity.type)}>
                  {ActivityTypeLabels[activity.type]}
                </Badge>
                {isToday && (
                  <Badge variant="outline" className="text-blue-600 border-blue-600">
                    Hoy
                  </Badge>
                )}
                {isPast && !isToday && (
                  <Badge variant="outline" className="text-gray-600">
                    Pasado
                  </Badge>
                )}
              </div>
              <h3 className="font-semibold text-lg text-slate-900 mb-1">
                {activity.title}
              </h3>
              <p className="text-slate-600 mb-2">
                {activity.description}
              </p>
            </div>
            <div className="flex space-x-2 ml-4">
              <AlertDialog>
                <AlertDialogTrigger asChild>
                  <Button variant="outline" size="sm" className="text-red-600 hover:text-red-700">
                    Eliminar
                  </Button>
                </AlertDialogTrigger>
                <AlertDialogContent>
                  <AlertDialogHeader>
                    <AlertDialogTitle>¿Eliminar actividad?</AlertDialogTitle>
                    <AlertDialogDescription>
                      Esta acción no se puede deshacer. Se eliminará permanentemente la actividad "{activity.title}".
                    </AlertDialogDescription>
                  </AlertDialogHeader>
                  <AlertDialogFooter>
                    <AlertDialogCancel>Cancelar</AlertDialogCancel>
                    <AlertDialogAction
                      onClick={() => handleDeleteActivity(activity.id)}
                      className="bg-red-600 hover:bg-red-700"
                    >
                      Eliminar
                    </AlertDialogAction>
                  </AlertDialogFooter>
                </AlertDialogContent>
              </AlertDialog>
            </div>
          </div>
          
          <div className="space-y-2 text-sm">
            <div className="flex items-center space-x-4 text-slate-600">
              <div>
                <span className="font-medium">Fecha:</span> {formatDate(activity.date)}
              </div>
              <div>
                <span className="font-medium">Hora:</span> {new Date(activity.date).toLocaleTimeString('es-ES', { hour: '2-digit', minute: '2-digit' })}
              </div>
            </div>
            
            {contactName && (
              <div className="text-slate-600">
                <span className="font-medium">Contacto:</span> {contactName}
              </div>
            )}
            
            {opportunityTitle && (
              <div className="text-slate-600">
                <span className="font-medium">Oportunidad:</span> {opportunityTitle}
              </div>
            )}
            
            <div className="text-xs text-slate-500 mt-2">
              Registrado: {formatDate(activity.createdAt)}
            </div>
          </div>
        </CardContent>
      </Card>
    );
  };

  // Get activity statistics
  const totalActivities = state.activities.length;
  const todayActivities = state.activities.filter(activity => 
    new Date(activity.date).toDateString() === new Date().toDateString()
  ).length;
  const thisWeekActivities = state.activities.filter(activity => {
    const activityDate = new Date(activity.date);
    const today = new Date();
    const weekStart = new Date(today.setDate(today.getDate() - today.getDay()));
    return activityDate >= weekStart;
  }).length;

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
        <div>
          <h1 className="text-3xl font-bold text-slate-900">Registro de Actividades</h1>
          <p className="text-slate-600">Historial de llamadas, correos, reuniones y tareas</p>
        </div>
        <ActivityForm
          trigger={
            <Button size="lg">
              Registrar Actividad
            </Button>
          }
        />
      </div>

      {/* Statistics */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        <Card>
          <CardContent className="p-4">
            <div className="text-2xl font-bold text-slate-900">{totalActivities}</div>
            <div className="text-sm text-slate-600">Total Actividades</div>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="p-4">
            <div className="text-2xl font-bold text-blue-600">{todayActivities}</div>
            <div className="text-sm text-slate-600">Actividades Hoy</div>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="p-4">
            <div className="text-2xl font-bold text-green-600">{thisWeekActivities}</div>
            <div className="text-sm text-slate-600">Esta Semana</div>
          </CardContent>
        </Card>
      </div>

      {/* Filters */}
      <Card>
        <CardContent className="p-4">
          <div className="grid grid-cols-1 md:grid-cols-5 gap-4">
            <div>
              <Input
                placeholder="Buscar actividades..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
              />
            </div>
            <div>
              <Select value={filterType} onValueChange={setFilterType}>
                <SelectTrigger>
                  <SelectValue placeholder="Filtrar por tipo" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="">Todos los tipos</SelectItem>
                  {Object.values(ActivityType).map(type => (
                    <SelectItem key={type} value={type}>
                      {ActivityTypeLabels[type]}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
            <div>
              <Input
                type="date"
                placeholder="Fecha inicio"
                value={startDate}
                onChange={(e) => setStartDate(e.target.value)}
              />
            </div>
            <div>
              <Input
                type="date"
                placeholder="Fecha fin"
                value={endDate}
                onChange={(e) => setEndDate(e.target.value)}
              />
            </div>
            <div>
              <Button variant="outline" onClick={clearFilters} className="w-full">
                Limpiar Filtros
              </Button>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Results Summary */}
      <div className="flex justify-between items-center">
        <p className="text-slate-600">
          {filteredActivities.length} actividad{filteredActivities.length !== 1 ? 'es' : ''} encontrada{filteredActivities.length !== 1 ? 's' : ''}
        </p>
      </div>

      {/* Activities List */}
      <div className="space-y-4">
        {filteredActivities.map(activity => (
          <ActivityCard key={activity.id} activity={activity} />
        ))}
      </div>

      {/* Empty State */}
      {filteredActivities.length === 0 && (
        <Card>
          <CardContent className="p-8 text-center">
            <h3 className="text-lg font-semibold text-slate-900 mb-2">
              No se encontraron actividades
            </h3>
            <p className="text-slate-600 mb-4">
              {searchQuery || filterType || startDate || endDate
                ? 'Intenta ajustar los filtros de búsqueda'
                : 'Comienza registrando tu primera actividad'}
            </p>
            {!searchQuery && !filterType && !startDate && !endDate && (
              <ActivityForm
                trigger={
                  <Button>
                    Registrar Primera Actividad
                  </Button>
                }
              />
            )}
          </CardContent>
        </Card>
      )}
    </div>
  );
}
