'use client';

import { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { useCRMStore } from '@/hooks/useCRMStore';
import { Activity, ActivityType, ActivityTypeLabels } from '@/lib/crmTypes';

interface ActivityFormProps {
  activity?: Activity;
  onClose?: () => void;
  trigger?: React.ReactNode;
  defaultContactId?: string;
  defaultOpportunityId?: string;
}

export function ActivityForm({ activity, onClose, trigger, defaultContactId, defaultOpportunityId }: ActivityFormProps) {
  const { addActivity, state } = useCRMStore();
  const [open, setOpen] = useState(false);
  const [formData, setFormData] = useState({
    type: activity?.type || ActivityType.LLAMADA,
    title: activity?.title || '',
    description: activity?.description || '',
    date: activity?.date 
      ? new Date(activity.date.getTime() - activity.date.getTimezoneOffset() * 60000).toISOString().slice(0, 16)
      : new Date(Date.now() - new Date().getTimezoneOffset() * 60000).toISOString().slice(0, 16),
    contactId: activity?.contactId || defaultContactId || '',
    opportunityId: activity?.opportunityId || defaultOpportunityId || ''
  });
  const [errors, setErrors] = useState<Record<string, string>>({});

  const validateForm = () => {
    const newErrors: Record<string, string> = {};
    
    if (!formData.title.trim()) {
      newErrors.title = 'El título es obligatorio';
    }
    
    if (!formData.description.trim()) {
      newErrors.description = 'La descripción es obligatoria';
    }
    
    if (!formData.date) {
      newErrors.date = 'La fecha es obligatoria';
    }
    
    if (!formData.contactId && !formData.opportunityId) {
      newErrors.relation = 'Debe asociar la actividad a un contacto o una oportunidad';
    }
    
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!validateForm()) {
      return;
    }

    const activityData = {
      ...formData,
      date: new Date(formData.date),
      contactId: formData.contactId || undefined,
      opportunityId: formData.opportunityId || undefined
    };

    addActivity(activityData);

    setFormData({
      type: ActivityType.LLAMADA,
      title: '',
      description: '',
      date: new Date(Date.now() - new Date().getTimezoneOffset() * 60000).toISOString().slice(0, 16),
      contactId: '',
      opportunityId: ''
    });
    setOpen(false);
    onClose?.();
  };

  const getContactName = (contactId: string): string => {
    const contact = state.contacts.find(c => c.id === contactId);
    return contact ? `${contact.name} - ${contact.company}` : 'Contacto no encontrado';
  };

  const getOpportunityTitle = (opportunityId: string): string => {
    const opportunity = state.opportunities.find(o => o.id === opportunityId);
    return opportunity ? opportunity.title : 'Oportunidad no encontrada';
  };

  const formContent = (
    <form onSubmit={handleSubmit} className="space-y-4">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <div className="space-y-2">
          <Label htmlFor="type">Tipo de Actividad</Label>
          <Select 
            value={formData.type} 
            onValueChange={(value: ActivityType) => setFormData(prev => ({ ...prev, type: value }))}
          >
            <SelectTrigger>
              <SelectValue />
            </SelectTrigger>
            <SelectContent>
              {Object.values(ActivityType).map(type => (
                <SelectItem key={type} value={type}>
                  {ActivityTypeLabels[type]}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>
        
        <div className="space-y-2">
          <Label htmlFor="date">Fecha y Hora *</Label>
          <Input
            id="date"
            type="datetime-local"
            value={formData.date}
            onChange={(e) => setFormData(prev => ({ ...prev, date: e.target.value }))}
            className={errors.date ? 'border-red-500' : ''}
          />
          {errors.date && <p className="text-sm text-red-500">{errors.date}</p>}
        </div>
      </div>
      
      <div className="space-y-2">
        <Label htmlFor="title">Título *</Label>
        <Input
          id="title"
          value={formData.title}
          onChange={(e) => setFormData(prev => ({ ...prev, title: e.target.value }))}
          className={errors.title ? 'border-red-500' : ''}
          placeholder="Ej: Llamada de seguimiento, Reunión de presentación..."
        />
        {errors.title && <p className="text-sm text-red-500">{errors.title}</p>}
      </div>
      
      <div className="space-y-2">
        <Label htmlFor="description">Descripción *</Label>
        <Textarea
          id="description"
          value={formData.description}
          onChange={(e) => setFormData(prev => ({ ...prev, description: e.target.value }))}
          className={errors.description ? 'border-red-500' : ''}
          rows={3}
          placeholder="Describe los detalles de la actividad..."
        />
        {errors.description && <p className="text-sm text-red-500">{errors.description}</p>}
      </div>
      
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <div className="space-y-2">
          <Label htmlFor="contactId">Contacto Asociado</Label>
          <Select 
            value={formData.contactId} 
            onValueChange={(value) => setFormData(prev => ({ ...prev, contactId: value }))}
          >
            <SelectTrigger>
              <SelectValue placeholder="Seleccionar contacto" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="">Sin contacto asociado</SelectItem>
              {state.contacts.map(contact => (
                <SelectItem key={contact.id} value={contact.id}>
                  {getContactName(contact.id)}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>
        
        <div className="space-y-2">
          <Label htmlFor="opportunityId">Oportunidad Asociada</Label>
          <Select 
            value={formData.opportunityId} 
            onValueChange={(value) => setFormData(prev => ({ ...prev, opportunityId: value }))}
          >
            <SelectTrigger>
              <SelectValue placeholder="Seleccionar oportunidad" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="">Sin oportunidad asociada</SelectItem>
              {state.opportunities.map(opportunity => (
                <SelectItem key={opportunity.id} value={opportunity.id}>
                  {getOpportunityTitle(opportunity.id)}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>
      </div>
      
      {errors.relation && (
        <p className="text-sm text-red-500">{errors.relation}</p>
      )}
      
      <div className="flex justify-end space-x-2 pt-4">
        <Button type="button" variant="outline" onClick={() => setOpen(false)}>
          Cancelar
        </Button>
        <Button type="submit">
          Registrar Actividad
        </Button>
      </div>
    </form>
  );

  if (trigger) {
    return (
      <Dialog open={open} onOpenChange={setOpen}>
        <DialogTrigger asChild>
          {trigger}
        </DialogTrigger>
        <DialogContent className="max-w-2xl">
          <DialogHeader>
            <DialogTitle>
              Registrar Nueva Actividad
            </DialogTitle>
          </DialogHeader>
          {formContent}
        </DialogContent>
      </Dialog>
    );
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle>Registrar Nueva Actividad</CardTitle>
      </CardHeader>
      <CardContent>
        {formContent}
      </CardContent>
    </Card>
  );
}
